.modal-window {
  position: fixed;
  z-index: 5;
  top: 0;
  left: 0;

  display: none;
  width: 100%;
  height: 100%;

  background-color: fade(#000000, 30%);

  &--opened {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

.modal-window__content {
  text-align: center;
  width: 320px;

  background-color: @main-bcg;

  &--error {
    padding: 45px 0 35px;
    min-height: 280px;

    .modal-window__title {
      @media (min-width: @tablet-width) {
        max-width: 307px;
      }
    }

    .modal-window__text {
      margin: 0 auto 28px;
      max-width: 237px;

      @media (min-width: @tablet-width) {
        margin: 0 auto 46px;
        max-width: 307px;
      }

      @media (min-width: @tablet-width) {
        margin: 0 auto 52px;
      }
    }

    @media (min-width: @tablet-width) {
      padding: 54px 0 49px;
      width: 400px;
      min-height: 347px;
    }

    @media (min-width: @desktop-width) {
      padding: 48px 0 49px;
    }
  }

  &--success {
    padding: 44px 0 0;
    min-height: 323px;

    .modal-window__button {
      width: 100%;
    }

    .modal-window__text {
      margin: 0 auto 37px;
      max-width: 244px;

      @media (min-width: @tablet-width) {
        margin: 0 auto 54px;
        max-width: 444px;
      }

      @media (min-width: @tablet-width) {
        margin: 0 auto 32px;
      }
    }

    @media (min-width: @tablet-width) {
      padding: 53px 0 0;
      width: 640px;
      min-height: 347px;
    }

    @media (min-width: @desktop-width) {
      padding: 51px 0 0;
    }
  }
}

.modal-window__title {
  padding: 0 20px 0;
  margin: 0 auto 39px;

  @media (min-width: @tablet-width) {
    font-size: 30px;
    line-height: 40px;
  }
}

.modal-window__text {
  color: @text-secondary;
}

.modal-window__button-wrapper {
  background-color: @form-input;
  width: 100%;
  padding: 20px;
  margin: auto 0 0;

  @media (min-width: @tablet-width) {
    padding: 44px 71px;
  }
}

.modal-window__button {
  padding: 18px 16px 13px;
  margin: 0 auto;
  min-width: 180px;
}
