/*Переменные*/
@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/ptsans.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/ptsansbold.woff2") format("woff2");
  font-display: swap;
}
/*Общая стилизация*/
*,
*::before,
*::after {
  box-sizing: border-box;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}
.button {
  display: block;
  max-width: 458px;
  /*width: 100%;*/
  border: none;
  background-color: #81b3d2;
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  color: #ffffff;
  text-decoration: none;
  text-transform: uppercase;
  text-align: center;
}
.button:hover {
  background-color: #669ec0;
}
.button:active {
  background-color: #5496BD;
  color: rgba(255, 255, 255, 0.3);
}
.button:disabled,
.button--disabled {
  background-color: #e5e5e5;
}
.page {
  display: flex;
  min-width: 320px;
  width: 100%;
  min-height: 100%;
  background-color: #e7e5e5;
}
.page__body {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 1200px;
  min-height: 100%;
  margin: 0 auto;
  background-color: #ffffff;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);
  font-family: "PT Sans", "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  text-transform: uppercase;
}
.page__main {
  flex-grow: 1;
  height: 100%;
}
/*Блоки*/
.page-header {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding: 0 20px;
  min-height: 56px;
}
.page-header--nojs {
  min-height: unset;
  padding: 0;
}
.page-header--nojs .page-header__toggle {
  display: none;
}
.page-header--nojs .page-header__nav {
  width: 100%;
}
@media (max-width: 767px) {
  .page-header--nojs .page-header__logo {
    z-index: unset;
  }
}
@media (min-width: 768px) {
  .page-header {
    min-height: unset;
    justify-content: unset;
    padding: 0;
  }
  .page-header .page-header__nav {
    width: 100%;
  }
}
.page-header__toggle {
  position: relative;
  z-index: 5;
  width: 23px;
  height: 22px;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.page-header__toggle:hover {
  opacity: 0.6;
}
.page-header__toggle:active {
  opacity: 0.3;
}
.page-header__toggle--opened .page-header__cross {
  display: flex;
}
.page-header__toggle--opened .page-header__burger {
  display: none;
}
@media (min-width: 768px) {
  .page-header__toggle {
    display: none;
  }
}
.page-header__cross {
  display: none;
}
.page-header__logo {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  top: 0;
  z-index: 3;
}
.site-nav {
  position: absolute;
  z-index: 4;
  left: 0;
  top: 0;
  display: flex;
  flex-direction: column;
  padding: 0;
  margin: 0;
  width: 100%;
  list-style: none;
}
.site-nav--closed {
  display: none;
}
.site-nav--nojs {
  position: relative;
  display: flex;
}
.site-nav::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 7px;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
}
@media (min-width: 768px) {
  .site-nav::after {
    display: none;
  }
}
@media (min-width: 768px) {
  .site-nav {
    position: static;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    margin: 0;
    background-color: #ffffff;
  }
}
.site-nav__item {
  display: flex;
  text-align: center;
  justify-content: center;
  background-color: #81b3d2;
}
.site-nav__item:hover {
  background-color: #669ec0;
}
.site-nav__item:active {
  background-color: #5496BD;
  color: rgba(255, 255, 255, 0.3);
}
.site-nav__item--index:hover,
.site-nav__item--gallery:hover,
.site-nav__item--form:hover {
  background-color: #81b3d2;
}
.site-nav__item--index:active,
.site-nav__item--gallery:active,
.site-nav__item--form:active {
  background-color: #81b3d2;
  color: #ffffff;
}
.site-nav__item:first-child {
  box-shadow: 0 1px 0 rgba(255, 255, 255, 0.2);
}
.site-nav__item:not(:first-child):not(:last-child) {
  box-shadow: 0 -1px 0 rgba(1, 1, 1, 0.2), 0 1px 0 rgba(255, 255, 255, 0.2);
}
.site-nav__item:last-child {
  box-shadow: 0 -1px 0 rgba(1, 1, 1, 0.2);
}
@media (min-width: 768px) {
  .site-nav__item {
    width: 20%;
    padding: 17px 0 12px;
    background-color: unset;
  }
  .site-nav__item:hover,
  .site-nav__item:active {
    background-color: unset;
  }
  .site-nav__item:first-child,
  .site-nav__item:not(:first-child):not(:last-child),
  .site-nav__item:last-child {
    box-shadow: none;
  }
  .site-nav__item:nth-child(4n+3) {
    margin: 0 0 0 20%;
  }
}
.site-nav__link {
  color: #ffffff;
  text-decoration: none;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  padding: 17px 49px 12px;
}
.site-nav__link:hover {
  background-color: #669ec0;
}
.site-nav__link:active {
  background-color: #5496BD;
  color: rgba(255, 255, 255, 0.3);
}
.site-nav__link--current {
  color: rgba(255, 255, 255, 0.6);
  cursor: default;
}
.site-nav__link--current:hover {
  background-color: #81b3d2;
}
.site-nav__link--current:active {
  background-color: #81b3d2;
  color: #ffffff;
}
@media (min-width: 768px) {
  .site-nav__link {
    padding: 0;
    color: #000000;
    font-size: 14px;
    line-height: 21px;
    background-color: unset;
  }
  .site-nav__link:hover {
    background-color: #ffffff;
    color: rgba(118, 99, 87, 0.6);
  }
  .site-nav__link:active {
    background-color: #ffffff;
    color: rgba(118, 99, 87, 0.3);
  }
  .site-nav__link--current {
    text-decoration: none;
    color: #766357;
  }
  .site-nav__link--current:hover {
    color: #766357;
  }
  .site-nav__link--current:active {
    color: #766357;
  }
}
