/* CONTROL-MARK */
.review-form__checkbox-label,
.review-form__radio-label {
  display: block;
  position: relative;
  padding-left: 62px;
  margin: 0;
  cursor: pointer;
}
.control-mark {
  position: absolute;
  width: 46px;
  height: 46px;
  top: -9px;
  left: 0;
  border-radius: 4px;
  background-color: #f2f2f2;
}
.control-mark--radio {
  border-radius: 50%;
}
.review-form__checkbox-input:checked + .control-mark::after {
  content: "";
  position: absolute;
  width: 22px;
  height: 16px;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  background-image: url("../img/sprite.svg#tick");
  background-repeat: no-repeat;
  background-position: center;
}
.review-form__radio-input:checked + .control-mark::after {
  content: "";
  position: absolute;
  width: 16px;
  height: 16px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 50%;
  background-color: #333333;
}
/* СОСТОЯНИЯ CONTROL */
.review-form__checkbox-input:hover ~ .control-mark,
.review-form__radio-input:hover ~ .control-mark {
  background-color: #e5e5e5;
}
.review-form__checkbox-input:focus ~ .control-mark,
.review-form__radio-input:focus ~ .control-mark {
  background-color: #ffffff;
  outline: 4px solid #e5e5e5;
}
.review-form__checkbox-input:active ~ .control-mark,
.review-form__radio-input:active ~ .control-mark {
  background-color: #d5d5d5;
}
.review-form__checkbox-input:active ~ .control-mark::after,
.review-form__radio-input:active ~ .control-mark::after {
  opacity: 0.3;
}
.review-form__checkbox-input:disabled ~ .control-mark,
.review-form__radio-input:disabled ~ .control-mark {
  opacity: 0.2;
  cursor: default;
}
